export default {
  data() {
    return {
      inputText: '',
    }
  },
  head() {
    return {
      title: 'Wasateam Interview | Eide',
      meta: [
        {
          hid: `description`,
          name: 'description',
          content: `This page shows more information about Eide.`,
        },
      ],
    }
  },
  computed: {
    content() {
      if (this.inputText === 'Vtuber') {
        return 'Usada Pekora'
      } else if (this.inputText === 'Pet') {
        return 'hamster'
      } else {
        return 'Please check your word'
      }
    },
  },
}
